import React from "react"
import { StaticQuery, graphql } from "gatsby"
import ColorModeSwitcher from "Components/modules/colorModeSwitcher"

export default function IndexPage() {
  return (
    <StaticQuery
      query={graphql`
        query {
          site {
            siteMetadata {
              title
            }
          }
        }
      `}
      render={data => (
        <>
          <h1>{data.site.siteMetadata.title}</h1>
          <ColorModeSwitcher text="Switch color mode" />
          <button
            type="button"
            className="btn btn-primary ml-2"
            data-toggle="tooltip"
            data-placement="auto"
            data-trigger="click"
            title="Tooltip on bottom"
          >
            Tooltip on bottom
          </button>
        </>
      )}
    />
  )
}
